<template>
  <div class="form">
    <h1>Welcome to catmash!</h1>
    <h2>Vote for the cutest kitten</h2>
    <ul id="cats">

    </ul>
  </div>
</template>

<script>
export default {
  	name: 'Form',
	data: {
	    cats: []
	  },
	  mounted() {
	    var self = this;
	    $.getJSON('https://latelier.co/data/cats.json', function(data) {
	      self.cats = data;
	    });

	    for(i = 0; i < Object.keys(cats).length; i++)
			$("#cats").append("<li>" + JSON.stringify(Object.keys(cats)[i]) + "</li>");

  	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



